<template>
	<div  class="CheckOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>按装运复核-首页</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">				
				<span>装运号</span>
				<input  placeholder="请输入内容" @keyup.enter="check"  @focus="g_focus('OrderKey')"  ref="OrderKey"  v-model="OrderKey" ></input>
				<i v-if="OrderKey" @click="clearInput('OrderKey')" class="iconfont icon-cuowu"></i>									
			</div>
			<div class="import">				
				<span>箱号</span>
				<input  placeholder="请输入内容" @keyup.enter="CheckStart"  @focus="g_focus('CaseNo')"    ref="CaseNo"  v-model="CaseNo" ></input>
				<i v-if="CaseNo" @click="clearInput('CaseNo')" class="iconfont icon-cuowu"></i>				
			</div>
			<div class="import">				
				<span>商品总数</span>
				<input    ref="SumQty"  v-model="SumQty" readonly></input>			
				</div>
			<div class="import">				
				<span>商品品相数</span>
				<input      ref="CountSku"  v-model="CountSku" readonly></input>				
			</div>
			<div class="import">			
				<span>待复核总数</span>
				<input    ref="SumPreCheckQty"  v-model="SumPreCheckQty" readonly></input>							
			</div>							
			<div class="g_doubleBtn">
				<div @click="CheckStart">开始复核</div>
				<div @click="checkDetail">查看明细</div>
			</div>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckLoad",
		data(){
			return{
				OrderKey:'',
				CaseNo:'',
				SumQty:'',//商品总数
				CountSku:'',//商品品相数
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
				StorerKey:'',
				CheckOrder:"",			
				StoreKey_select:"",
				BoxType:'ByLoad',
				SumCheckedQty:'',//已复核总数
			}
		},
		computed:{
			SumPreCheckQty:function(){
				return this.SumQty-this.SumCheckedQty;
			}
		},
		mounted() {
			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
			this.$refs['OrderKey'].focus();

			if(this.OrderKey){
            	this.check();
            	if(this.CaseNo){
            		this.$refs['CaseNo'].select();
            	}
            }
		},
		created(){
			var that = this;
			// SO单号
			if(this.$route.query.OrderKey){

				that.OrderKey = that.$route.query.OrderKey;
			}
			//箱号
			if(this.$route.query.CaseNo){

				that.CaseNo = that.$route.query.CaseNo;
			}
//			that.check();
		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				if(inputName == "OrderKey") {

					this.check();
					
				}else if(inputName == "CaseNo") {

					this.CheckStart();
				}
			},
			//返回上一页 首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
			// 获取复核单总数
			check:function(){
			var that = this;
			if(this.OrderKey =='') {
 				that.msg = "装运号不能为空";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
 			 }

				var currentUrl = this.HOST + "RFCheck.do";

				var postData = {
					OrderKey : that.OrderKey,
					BoxType:that.BoxType,
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){
						var sumQty=0;
						var sumCountSky=0;
						for(var i=0;i<res.data.data.length;i++){
							sumQty=sumQty+res.data.data[i].SumQty;
							sumCountSky=sumCountSky+res.data.data[i].CountSku;
						}						
                        that.SumQty=sumQty;
                        that.CountSku=sumCountSky;
                        that.SumCheckedQty=res.data.data[0].SumCheckedQty;
						that.StorerKey=res.data.data[0].StorerKey;
						that.OrderKey=res.data.data[0].ExternalLoadId;
						that.$refs['CaseNo'].focus();
					}else{

						that.tipsShow("");

						that.msg = "未查询到数据";

						that.inputFocus = "Orderkey";
					}
				},"OrderKey")
			},
			
			//开始复核--跳转到 按单复核-复核页面
			CheckStart:function(){

				var that = this;
				
			if(this.OrderKey =='') {
 				that.msg = "装运号不能为空";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
 			 }
			
			    if(this.Status=='02' || this.Status=='04' || this.Status=='09'){				
 				that.msg = "订单未配货,无法复核";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
				}
				if(this.Status=='98' || this.Status=='99'){
				that.msg = "订单已取消,无需复核";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
				}
				if(this.SumPreCheckQty<=0){
				that.msg = "订单已经复核完成,无需再次复核!";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
				}

				var currentUrl = this.HOST + "RFCheckStart.do";

				var postData = {
					OrderKey : that.OrderKey,
					 BoxType : that.BoxType,
				    StorerKey: that.StorerKey,
					   CaseNo: that.CaseNo,
				}

				this.httpRequest(currentUrl,postData,function(res){										
						that.$router.push({
						name:"CheckLoad_check",
							query:{
								OrderKey : that.OrderKey,
								       CaseNo: that.CaseNo,
								       StorerKey:that.StorerKey,
							}
						})
					
				},)
			},
			//按单复核-查看明细
		checkDetail:function(){
			var that = this;					
			if(this.OrderKey =='') {
 				that.msg = "装运号不能为空";
				that.tipsShow("OrderKey");
				that.inputFocus = "OrderKey";
				return false;
 			}
				var currentUrl = this.HOST + "RFCheckDetail.do";

				var postData = {
					OrderKey : that.OrderKey,
					BoxType : that.BoxType,
				}

				this.httpRequest(currentUrl,postData,function(res){
					if(res.data.data.length){
						that.$store.state.CheckOrder_detailData = res.data.data;
						that.$router.push({
						name:"CheckLoad_detail",
						query:{
								OrderKey : that.OrderKey,
							}
						})
					}else{

						that.tipsShow("");

						that.msg = "未查询到数据";

					}
					
				},)
			}
			
		}
	}
</script>
<style scoped lang="less">
.CheckOrder{
	&>.container{
		margin-top: 110/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}		
		
		&>div.import{
			width:100%;
			height: 70/50rem;
			margin-bottom: 20/50rem;
			box-sizing: border-box;
			position: relative;
			overflow: hidden;
			span{
				width: 150/50rem;
				margin-top: 10/50rem;
				text-align: right;
				float: left;
				height:65/50rem;
				margin-left: 30/50rem;
			}
			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				float: left;				
				padding:0 10/50rem;
			}	
			input[readonly]{
				background-color: #F0F0F0;
				outline: none;
			}				
			i{
				position:absolute;
				right:70/50rem;
				font-size: 30/50rem;
				top: 15/50rem;
			}							    			
		}
	}
}
@media screen and (max-width: 320px){
	.CheckOrder{
		&>.container{
			font-size: 38/50rem;
			margin-top: 110/50rem;
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div.import{
				height: 75/50rem;
				padding: 0;
				span{
					width: 190/50rem;
					height: 60/50rem;
					line-height: 60/50rem;
					padding-right: 10/50rem;
				}				
				input{
					width:420/50rem;	
					height:60/50rem;
					line-height: 65/50rem;
					margin: 0;
				}			
				i{
					position:absolute;
					right:120/50rem;
					font-size: 42/50rem;
					top: 26/50rem;
				}							
			}
			&>div.top{
				margin-top: 20/50rem;
			}
			&>div.top1{
				margin-top: 10/50rem;
			}
			
			&>div.butto{
				margin-bottom: 0;
			}
		}
	}
}
@media screen and (max-width: 329px) and (min-width: 291px){
	.CheckOrder{
		&>.container{
			&>div.import{
				input{					
					width:440/50rem;
				}
				i{
					position:absolute;
					right:80/50rem;
					font-size: 45/50rem;
					top: 10/50rem;
				}
				
			}
		}
	}
}
@media screen and (max-width: 290px){
	.CheckOrder{
		&>.container{
			font-size: 42/50rem;
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div.import{
				height: 85/50rem;
				line-height: 85/50rem;	
				span{
					width: 210/50rem;
					height: 75/50rem;
					line-height: 75/50rem;
					padding-right: 5/50rem;
					padding-left: 0;
					margin: 0;
				}
							
				input{					
					width:490/50rem;	
					height:75/50rem;
					margin: 0;
					padding: 0 10/50rem;
					box-sizing: border-box;
				}
				i{
					position:absolute;
					right:80/50rem;
					font-size: 45/50rem;
					top: -2/50rem;
				}
				
			}
		}
	}
}
</style>


